.resp-tab-content h4 {
	font-size: 14px;
}
.slide-item:before {
	content: '\e96b';
	margin-left: 8px;
	position: relative;
	font-family: 'feather' !important;
	opacity: 0.6;
}
.first-sidemenu {
	position: fixed;
	width: 100px;
	background: $white;
	height: 100%;
	z-index: 5;
	padding-bottom: 70px;
}
.second-sidemenu {
	position: fixed;
	width: 200px;
	background: $white;
	height: 100%;
	right: 100px;
	z-index: 4;
	padding-bottom: 70px;
	box-shadow: 6px 16px 31px -18px #c2bad2;
}
.side-menu .slide {
	&.is-expanded .angle {
		-webkit-transform: rotate(90deg);
		-ms-transform: rotate(90deg);
		transform: rotate(90deg);
	}
	&.submenu a {
		color: #191919;
		padding: 7px 7px;
		margin: 0;
	}
}
.slide.submenu .side-menu__label:before {
	content: '\e96b';
	margin-left: 8px;
	position: relative;
	font-family: 'feather' !important;
	opacity: 0.6;
}
.side-menu .slide.submenu a {
	i {
		margin-left: 4px;
	}
	.side-menu__label {
		text-align: inherit;
	}
}
.slide.submenu .side-menu__label:hover {
	color: $primary;
}
.side-menu .slide-menu.submenu-list {
	margin-bottom: 0;
	margin-right: 12px;
	li:hover {
		border-bottom: 0 !important;
	}
	a:hover {
		color: $primary;
	}
}
.first-sidemenu .resp-vtabs li.active {
	border-left: 1px solid transparent !important;
}
.app.sidebar-mini {
	&.sidenav-toggled4 {
		.second-sidemenu {
			visibility: visible;
		}
		.app-content {
			margin-right: 100px;
		}
		&.sidebar-gone .first-sidemenu {
			visibility: visible;
			right: 0;
		}
	}
	&.sidenav-toggled .side-menu .side-menu__icon {
		margin-left: 0;
		margin-right: 0;
	}
}
