			color: #3e4b5b;
			border-top-left-radius: 4px;
			border-bottom-left-radius: 4px;
		}
		.slide {
			&:hover {
				.side-menu__item {
					overflow: hidden;
					border-right-color: transparent;
					background: #ececec;
					color: $white;
				}
				.side-menu__label {
					opacity: 0;
				}
				.slide-menu {
					max-height: 100vh;
					opacity: 0;
					visibility: visible;
					z-index: 10;
				}
			}
			.side-menu__label {
				border-bottom-left-radius: 0;
			}
		}
		.slide-menu {
			position: absolute;
			right: 50px;
			min-width: 180px;
			padding: 12px 0;
			opacity: 0;
			border-bottom-left-radius: 4px;
			z-index: 9;
			visibility: hidden;
			-webkit-transition: visibility 0.3s ease;
			-o-transition: visibility 0.3s ease;
			transition: visibility 0.3s ease;
		}
		.user-body img {
			width: 40px !important;
			text-align: center;
			margin: 0 auto;
		}
		.user-info {
			display: none;
		}
	}
}
