}
.toggle-menu.side-menu li {
	ul li {
		&:first-child {
			border-top: 1px solid rgba(107, 122, 144, 0.1) !important;
		}
		&:last-child a {
			border-bottom: 0 !important;
		}
		a {
			border: 0;
		}
	}
	a {
		border-top: 1px solid rgba(107, 122, 144, 0.1);

		&:hover ,&:focus {
			border-top: 1px solid rgba(210, 215 ,221 ,0.2);
			border-bottom: 1px solid rgba(210, 215 ,221 ,0.2);
		}
	}
	li a {
		&:hover ,&:focus{
			border-top: 0;
		}
	}
}
.slide-menu li.active>a {
	background: none !important;
}


/*-- Sub slide  ---*/
.sub-slide-menu{
	overflow: hidden;
    padding: 0;
    font-size: .8rem !important;
	display:none;
}
.sub-slide.is-expanded .sub-slide-menu{
    display: block;
}
.sub-slide .sub-slide-item{
	display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 8px 50px 8px 20px;
    font-size: 14px;
    color: #191919;
    border-bottom: 0;
}
.sub-slide .sub-slide-menu .sub-slide-item{
	padding: 8px 70px 8px 20px;
}
.sub-slide-item:before {
    content: '\e96b';
    margin-left: 8px;
    position: relative;
    font-family: 'feather' !important;
    opacity: 0.6;
}
.sub-side-menu__label {
    white-space: nowrap;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: block;
    font-weight: 400;
}
.sub-slide .sub-angle {
    -webkit-transform-origin: center;
    transform-origin: center;
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.sub-slide.is-expanded .sub-angle {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}
.sub-slide a:hover, .sub-slide a:focus{
	border:0 !important;
}
.toggle-menu.side-menu li ul li .sub-slide-menu li:first-child{
	border-top:0 !important;
}


